        </div>
        <el-tooltip class="tooltip" effect="dark" content="Click to logout" placement="left">
            <div class="floating-logout">
                <el-button type="danger" @click="logout" icon="el-icon-delete-solid" circle></el-button>
            </div>
        </el-tooltip>
        <el-tooltip class="tooltip" effect="dark" content="Click to go back to profile home page" placement="right">
            <div class="floating-home">
                <el-button type="warning" @click="home" icon="el-icon-s-home" circle></el-button>
            </div>
        </el-tooltip>
    </div>
</template>

<script>
import editProfile from "@/components/editProfile.vue";
import editEducation from "@/components/editEducation.vue";
import editExperience from "@/components/editExperience.vue";
import editProjects from "@/components/editProjects.vue";
import editSkills from "@/components/editSkill.vue";
import placeholder from "@/components/placeholder.vue";
import router from "./../router"
export default {
    components: {
        editProfile,
        editEducation,
        editExperience,
        editProjects,
        editSkills,
        placeholder
    },
    methods: {
        logout: function() {
            window.localStorage.clear();
            router.push('/');
        },
        home: function() {
            router.push('/');
        }
    },
    created: function(){
        var loggedin = Boolean(window.localStorage.getItem("loggedin"));
        if (loggedin != true) {
            router.push('/login');
        }
    }
}
</script>

