<template>
<div style="margin-bottom: 20px">
  <el-card v-if="show" class="single-skill" shadow="hover">
    <el-form ref="skillForm" :model="skillForm" :rules="rules" label-width="120px" label-position="left" status-icon>
        <el-form-item label="Skill Title" prop="title">
            <el-input v-model="skillForm.title" placeholder="Your skill title" clearable></el-input>
        </el-form-item>
        <el-form-item label="Description" prop="content">
            <el-input v-model="skillForm.content" type="textarea" prop="desc" placeholder="Sub skills..." maxlength="200" :autosize="{minRows: 2}" clearable show-word-limit></el-input>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" @click="updateSkill" size="small">Update</el-button>
            <el-button type="danger" @click="deleteSkill" size="small">Delete</el-button>
        </el-form-item>
    </el-form>
  </el-card>
</div>
</template>

<script>
import {db} from "./../firebase";
export default {
    props: [
        'skill',
        'fromArray'
    ],
    data() {
        return {
            skillForm: {
                title: "",
                content: ""
            },
            rules: {
                title: [
                    { required: true, message: "Please input your skill title!", trigger: "blur" }
                ]
            },
            show: true,
            docID: ""
        }
    },
    created: function() {
        if (this.$props.fromArray != undefined){
            this.skillForm.title = this.$props.skill.title;
            this.skillForm.content = this.$props.skill.content;
            this.docID = this.$props.skill.docID;
        }
    },
    methods: {
        updateSkill(){
            this.$refs.skillForm.validate((valid)=>{
                if (valid) {
                    if (!this.$props.fromArray) {
                        this.isFromArray = false;
                        this.docID = makeid(11);
                    }
                    db.collection('portfolio').doc('skills').collection('skills').doc(this.docID).set({
                        docID: this.docID,
                        title: this.skillForm.title,
                        content: this.skillForm.content
                    });       
                }
                this.$notify.success("Updated successfully!");

            });
        },
        deleteSkill(){
            if (!this.$props.fromArray) {
                this.isFromArray = false;
            } else {
                db.collection('portfolio').doc('skills').collection('skills').doc(this.docID).delete();
            }
            this.$notify.warning("Deleted successfully!");
        }
    },
    computed: {
        isFromArray: {
            get: function() {
                if (this.$props.fromArray == undefined) {
                    return false;
                }
                return this.$props.fromArray;
            },
            set: function(value) {
                this.show = value;
            }
        }
    }
}

function makeid(length) {
   var result           = '';
   var characters       = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
   var charactersLength = characters.length;
   for ( var i = 0; i < length; i++ ) {
      result += characters.charAt(Math.floor(Math.random() * charactersLength));
   }
   return result;
}
</script>