<template>
<div class="avatar-container">
        <div class="avatar-holder">
            <el-link @click="toggleUrl" :underline="false">
                <el-tooltip class="tooltip" effect="dark" content="Click to edit profile" placement="right">
                    <el-avatar :size="150" :src="profile.avatar" alt="Profile Photo"></el-avatar>
                </el-tooltip>
            </el-link>
        </div>
    <div style="margin-top: 20px;"><span>{{profile.name}}</span><span v-if="profile.cn_name!=''"> {{profile.cn_name}}</span></div>
    <el-link :href="profile.website" target="_blank" type="primary" :underline="false">{{profile.website}}</el-link>
    <div class="avatar-subtitle">
        <p id="subtitle"><i v-if="profile.subtitle === ''" class="el-icon-loading"></i><span v-else>{{profile.subtitle}}</span></p>
    </div>
</div>
   
</template>
<script>
import {db} from './../firebase';
import router from "./../router";
export default {
    data() {
        return {
            avatarToUrl: "/edit"
        }
    },
    firestore() {
        return {
            profile: db.collection('portfolio').doc('profile')
        }
    },
    methods: {
        toggleUrl(){
            console.log(this.avatarToUrl);
            switch (this.avatarToUrl){
                case "/edit":
                    router.push(this.avatarToUrl);
                    this.avatarToUrl = '/';
                    break;
                case "/":
                    router.push(this.avatarToUrl);
                    this.avatarToUrl = "/edit";
                    break;
            }
        }
    }
}
</script>
