    .avatar-subtitle {
        color: #555;
        opacity: .7;
        font-size: 1.5vw;
        text-align: center;
        margin-left: 10rem;
        margin-right: 10rem;
    }
    .avatar-container {
        text-align: center;
        font-size: 2vw;
    }
    .avatar-holder {
        display: flex;
        justify-content: center;
        padding-top: 2rem;
        transition: filter 1s;
    }
    .avatar-holder:hover {
        filter: drop-shadow(0 0 0.5rem #222);
    }

