<template>
  <div class="edit-profile">
    <el-form ref="profileForm" :model="profileForm" :rules="rules" label-width="120px" label-position="left" status-icon>
        <el-form-item label="Name" prop="en_name">
            <el-input v-model="profileForm.en_name" placeholder="English display name here" clearable></el-input>
        </el-form-item>
        <el-form-item label="中文姓名" prop="cn_name">
            <el-input v-model="profileForm.cn_name" placeholder="你的中文姓名……" clearable></el-input>
        </el-form-item>
        <el-form-item label="Email address" prop="email">
            <el-input v-model="profileForm.email" placeholder="Your email address" clearable></el-input>
        </el-form-item>
        <el-form-item label="Tagline/Intro" prop="subtitle">
            <el-input type="textarea" v-model="profileForm.subtitle" placeholder="A short description or tagline about yourself..." maxlength="200" clearable :autosize="{minRows: 2}" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="Resume" prop="resume">
            <el-input v-model="profileForm.resume" placeholder="Your Resume link" clearable></el-input>
        </el-form-item>
        <el-form-item label="Avatar" prop="avatar">
            <el-input v-model="profileForm.avatar" placeholder="Your Avatar link" clearable></el-input>
        </el-form-item>
        <el-form-item label="Website" prop="website">
            <el-input v-model="profileForm.website" placeholder="Your personal website link" clearable></el-input>
        </el-form-item>
        <el-form-item label="Github" prop="git">
            <el-input v-model="profileForm.git" placeholder="Your Github link" clearable></el-input>
        </el-form-item>
        <el-form-item label="Linkedin" prop="linkedin">
            <el-input v-model="profileForm.linkedin" placeholder="Your LinkedIn link" clearable></el-input>
        </el-form-item>
        <el-form-item label="YouTube" prop="youtube">
            <el-input v-model="profileForm.youtube" placeholder="Your YouTube channel link" clearable></el-input>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" @click="updateProfile">Update</el-button>
        </el-form-item>
    </el-form>
  </div>
</template>

<script>
import {db} from "./../firebase";
export default {
    data() {
        var validateCN = (rule, value, callback) => {
            if (value == ""){
                callback()
            } else if (!value.toString().match(/[\u3400-\u9FBF]/)) {
                callback(new Error("Input is not Chinese!"));
            } else {
                callback();
            }
        };
        return {
            profileForm: {
                en_name: "",
                cn_name: "",
                email: "",
                subtitle: "",
                website: "",
                git: "",
                youtube: "",
                linkedin: "",
                avatar: "",
                resume: ""
            },
            rules: {
                en_name: [
                    { required: true, message: "Please input your name in English", trigger: 'blur' }
                ],
                cn_name: [
                    { validator: validateCN, trigger: 'blur' }
                ],
                email: [
                    { required: true, message: "Please input your email address", trigger: 'blur' },
                    { type: 'email', message: 'Please input correct email address', trigger: ['blur', 'change']}
                ],
                subtitle: [
                    { required: true, message: "Please input some short description about yourself", trigger: 'blur' }
                ],
                avatar: [
                    { type: 'url', message: "Please input valid URL", trigger: ['blur', 'change']}
                ],
                website: [
                    { type: 'url', message: "Please input valid URL", trigger: ['blur', 'change']}
                ],
                git: [
                    { type: 'url', message: "Please input valid URL", trigger: ['blur', 'change']}
                ],
                linkedin: [
                    { type: 'url', message: "Please input valid URL", trigger: ['blur', 'change']}
                ],
                youtube: [
                    { type: 'url', message: "Please input valid URL", trigger: ['blur', 'change']}
                ],
                resume: [
                    { type: 'url', message: "Please input valid URL", trigger: ['blur', 'change']}
                ]
            }
        }
    },
    created: function () {
        db.collection('portfolio').doc('profile').get().then((doc)=>{
            var profile = doc.data();
            this.profileForm.en_name = profile.name;
            this.profileForm.cn_name = profile.cn_name;
            this.profileForm.subtitle = profile.subtitle;
            this.profileForm.website = profile.website;
            this.profileForm.avatar = profile.avatar;
        });
        db.collection('portfolio').doc('social').get().then((doc)=>{
            this.profileForm.email = doc.data().email.substring(7);
            this.profileForm.git = doc.data().git;
            this.profileForm.linkedin = doc.data().linkedin;
            this.profileForm.youtube = doc.data().youtube;
            this.profileForm.resume = doc.data().resume;
        })
    },
    methods: {
        updateProfile() {
            this.$refs.profileForm.validate((valid)=>{
                if (valid) {
                    // upload to firebase
                    db.collection('portfolio').doc('profile').set({
                        cn_name: this.profileForm.cn_name,
                        name: this.profileForm.en_name,
                        subtitle: this.profileForm.subtitle,
                        website: this.profileForm.website,
                        avatar: this.profileForm.avatar
                    });
                    db.collection('portfolio').doc('social').set({
                        email: "mailto:" + this.profileForm.email,
                        git: this.profileForm.git,
                        linkedin: this.profileForm.linkedin,
                        youtube: this.profileForm.youtube,
                        resume: this.profileForm.resume
                    });
                    this.$notify.success("Updated successfully!");
                }
            })
        }
    }
}
</script>