<template>
    <div class="edit-skill">
        <div class="add-skill">
            <el-button type="success" icon="el-icon-circle-plus-outline" @click="addSkill" plain>Add New Skill
            </el-button>
        </div>
        <div ref="exp" class="skill-section">
            <singleSkill v-for="skill in skills" :key="skill.docID" :skill="skill"
                :fromArray="true" />
        </div>
    </div>
</template>

<script>
    import singleSkill from "@/components/singleSkill.vue";
    import Vue from 'vue';
    import {
        db
    } from "./../firebase";
    export default {
        components: {
            singleSkill
        },
        firestore() {
            return {
                skills: db.collection('portfolio').doc('skills').collection('skills')
            }
        },
        methods: {
            addSkill() {
                var ComponentClass = Vue.extend(singleSkill);
                var instance = new ComponentClass();
                instance.$mount();
                this.$refs["exp"].appendChild(instance.$el);
            }
        }
    }

</script>

<style>
    .add-skill {
        display: flex;
        justify-content: center;
        margin-bottom: 20px;
    }
</style>