<template>
    <div class="edit-project">
        <div class="add-project">
            <el-button type="success" icon="el-icon-circle-plus-outline" @click="addProject" plain>Add New Project
            </el-button>
        </div>
        <div ref="exp" class="project-section">
            <singleProject v-for="project in projects" :key="project.id" :project="project"
                :fromArray="true" />
        </div>
    </div>
</template>

<script>
    import singleProject from "@/components/singleProject.vue";
    import Vue from 'vue';
    import {
        db
    } from "./../firebase";
    export default {
        components: {
            singleProject
        },
        firestore() {
            return {
                projects: db.collection('portfolio').doc('projects').collection('projects')
            }
        },
        methods: {
            addProject() {
                var ComponentClass = Vue.extend(singleProject);
                var instance = new ComponentClass();
                instance.$mount();
                this.$refs["exp"].appendChild(instance.$el);
            }
        }
    }

</script>

<style>
    .add-project {
        display: flex;
        justify-content: center;
        margin-bottom: 20px;
    }
</style>