<template>
    <div class="edit-experience">
        <div class="add-experience">
            <el-button type="success" icon="el-icon-circle-plus-outline" @click="addExperience" plain>Add New Experience
            </el-button>
        </div>
        <div ref="exp" class="experience-section">
            <singleExperience v-for="experience in sortedArray" :key="experience.docID" :experience="experience"
                :fromArray="true" />
        </div>
    </div>
</template>

<script>
    import singleExperience from "@/components/singleExperience.vue";
    import { compare } from './../scripts/compare';
    import Vue from 'vue';
    import {
        db
    } from "./../firebase";
    export default {
        components: {
            singleExperience
        },
        firestore() {
            return {
                experiences: db.collection('portfolio').doc('experience').collection('experience')
            }
        },
        methods: {
            addExperience() {
                var ComponentClass = Vue.extend(singleExperience);
                var instance = new ComponentClass();
                instance.$mount();
                this.$refs["exp"].appendChild(instance.$el);
            }
        },
        computed: {
            sortedArray: function() {
                var newArray = this.experiences.slice();
                return newArray.sort(compare);
            }
        }
    }
</script>

<style>
    .add-experience {
        display: flex;
        justify-content: center;
        margin-bottom: 20px;
    }
</style>