<template>
<div style="margin-bottom: 20px">
  <el-card v-if="show" class="single-experience" shadow="hover">
    <el-form ref="experienceForm" :model="experienceForm" :rules="rules" label-width="120px" label-position="left" status-icon>
        <el-form-item label="Company/Org" prop="title">
            <el-input v-model="experienceForm.title" placeholder="Your company/organization name" clearable></el-input>
        </el-form-item>
        <el-form-item label="Position" prop="position">
            <el-input v-model="experienceForm.position" placeholder="Your position" clearable></el-input>
        </el-form-item>
        <div class="location-container">
            <el-form-item label="Location" prop="city">
                <el-input v-model="experienceForm.city" placeholder="city" clearable></el-input>
            </el-form-item>
            <el-form-item label=" " prop="country" label-width="20px">
                <el-input v-model="experienceForm.country" placeholder="country" clearable></el-input>
            </el-form-item>
        </div>

        <div class="date-container">
            <el-form-item label="From Date" prop="fromDate">
                <el-date-picker v-model="experienceForm.fromDate" type="month" placeholder="Starting date" size="small" format="MMM yyyy"></el-date-picker>
            </el-form-item>
            <el-form-item label="To Date" prop="toDate" label-width="70px">
                <el-date-picker :disabled="disabled" v-model="experienceForm.toDate" type="month" placeholder="Ending date" size="small" format="MMM yyyy"></el-date-picker>
            </el-form-item>
            <el-form-item label="Present?" prop="present">
                <el-switch v-model="experienceForm.present"></el-switch>
            </el-form-item>
        </div>
        <el-form-item label="Description" prop="content">
            <el-input v-model="experienceForm.content" type="textarea" prop="content" placeholder="Description..." maxlength="800" :autosize="{minRows: 2}" clearable show-word-limit></el-input>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" @click="updateExperience" size="small">Update</el-button>
            <el-button type="danger" @click="deleteExperience" size="small">Delete</el-button>
        </el-form-item>
    </el-form>
  </el-card>
</div>
</template>

<script>
import {db} from "./../firebase";
export default {
    props: [
        'experience',
        'fromArray'
    ],
    data() {
        return {
            experienceForm: {
                title: "",
                position: "",
                city: "",
                country: "",
                from: -999,
                fromDate: "",
                toDate: "",
                present: false,
                content: ""
            },
            rules: {
                title: [
                    { required: true, message: "Please input your company/organization name!", trigger: "blur" }
                ],
                country: [
                    { required: true, message: "Please input country!", trigger: "blur" }
                ],
                fromDate: [
                    { required: true, message: "Please input start date!", trigger: "blur" }
                ],
                present: [
                    { required: true, message: "Please indicate if you are currently working here!", trigger: "blur" }
                ],
                position: [
                    { required: true, message: "Please input your position!", trigger: "blur" }
                ]
            },
            show: true,
            disabled: false,
            docID: ""
        }
    },
    created: function() {
        if (this.$props.fromArray != undefined){
            this.experienceForm.title = this.$props.experience.title;
            this.experienceForm.city = this.$props.experience.location_city;
            this.experienceForm.country = this.$props.experience.location_country;
            this.experienceForm.fromDate = this.$props.experience.fromDate;
            this.experienceForm.toDate = this.$props.experience.toDate;
            this.experienceForm.present = this.$props.experience.present;
            this.experienceForm.position = this.$props.experience.position;
            this.experienceForm.content = this.$props.experience.content;
            this.docID = this.$props.experience.docID;
        }
    },
    methods: {
        updateExperience(){
            this.$refs.experienceForm.validate((valid)=>{
                if (valid) {
                    if (!this.$props.fromArray) {
                        this.isFromArray = false;
                        this.docID = makeid(11);
                    }
                    var month = new Array();
                    month[0] = "Jan";
                    month[1] = "Feb";
                    month[2] = "Mar";
                    month[3] = "Apr";
                    month[4] = "May";
                    month[5] = "Jun";
                    month[6] = "Jul";
                    month[7] = "Aug";
                    month[8] = "Sep";
                    month[9] = "Oct";
                    month[10] = "Nov";
                    month[11] = "Dec";
                    var fromMonth = new Date(this.experienceForm.fromDate).getMonth();
                    var fromYear = new Date(this.experienceForm.fromDate).getFullYear();
                    var fromMonthStr = month[fromMonth];
                    var fromDate = fromMonthStr + ", " + String(fromYear);
                    var toMonth = new Date(this.experienceForm.toDate).getMonth();
                    var toYear = new Date(this.experienceForm.toDate).getFullYear();
                    var toMonthStr = month[toMonth];
                    var toDate = toMonthStr + ", " + String(toYear);
                    db.collection('portfolio').doc('experience').collection('experience').doc(this.docID).set({
                        docID: this.docID,
                        title: this.experienceForm.title,
                        location_city: this.experienceForm.city,
                        location_country: this.experienceForm.country,
                        position: this.experienceForm.position,
                        fromDate: fromDate,
                        toDate: toDate,
                        from: new Date(this.experienceForm.fromDate).getFullYear(),
                        present: this.experienceForm.present,
                        content: this.experienceForm.content
                    })
                    this.$notify.success("Updated successfully!");
                }
            });
        },
        deleteExperience(){
            if (!this.$props.fromArray) {
                this.isFromArray = false;
            } else {
                db.collection('portfolio').doc('experience').collection('experience').doc(this.docID).delete();
            }
            this.$notify.warning("Deleted successfully!");
        }
    },
    computed: {
        isFromArray: {
            get: function() {
                if (this.$props.fromArray == undefined) {
                    return false;
                }
                return this.$props.fromArray;
            },
            set: function(value) {
                this.show = value;
            }
        }
    },
    watch: {
        experienceForm: {
            handler(val) {
                if (val.present == true) {
                    this.disabled = true;
                } else {
                    this.disabled = false;
                }
            },
            deep: true
        }
    }
}
function makeid(length) {
   var result           = '';
   var characters       = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
   var charactersLength = characters.length;
   for ( var i = 0; i < length; i++ ) {
      result += characters.charAt(Math.floor(Math.random() * charactersLength));
   }
   return result;
}
</script>

<style scoped>
    .location-container {
        display: grid; 
        grid-column-gap: 50px; 
        grid-template-columns: auto auto;
    }
    .date-container {
        display: grid;
        grid-template-columns: auto auto;
        grid-column-gap: 10px;
    }

    .date-container .el-date-editor.el-input, .el-date-editor.el-input__inner {
        width: 100%;
    }

    @media(max-width: 768px){
        .date-container {
            display: block;
        }
        .location-container {
            display: block;
        }
    }

</style>