<template>
    <div class="edit-education">
        <div class="add-education">
            <el-button type="success" icon="el-icon-circle-plus-outline" @click="addEducation" plain>Add New Education
            </el-button>
        </div>
        <div ref="edu" class="education-section">
            <singleEducation v-for="education in sortedArray" :key="education.docID" :education="education"
                :fromArray="true" />
        </div>
    </div>
</template>

<script>
    import singleEducation from "@/components/singleEducation.vue";
    import { compare } from './../scripts/compare';
    import Vue from 'vue';
    import {
        db
    } from "./../firebase";
    export default {
        components: {
            singleEducation
        },
        firestore() {
            return {
                educations: db.collection('portfolio').doc('education').collection('education')
            }
        },
        methods: {
            addEducation() {
                var ComponentClass = Vue.extend(singleEducation);
                var instance = new ComponentClass();
                instance.$mount();
                this.$refs["edu"].appendChild(instance.$el);
            }
        },
        computed: {
            sortedArray: function() {
                var newArray = this.educations.slice();
                return newArray.sort(compare);
            }
        }
    }
</script>

<style>
    .add-education {
        display: flex;
        justify-content: center;
        margin-bottom: 20px;
    }
</style>