<template>
  <div style="margin-bottom: 20px">
    <el-card v-if="show" class="single-project" shadow="hover">
      <div class="thumbnail-preview">
        <el-image
          v-if="imageURL!=''"
          style="width: 300px; height: 200px"
          :src="imageURL"
          fit="contain"
        ></el-image>
      </div>
      <el-form
        ref="projectForm"
        :model="projectForm"
        :rules="rules"
        label-width="120px"
        label-position="left"
        status-icon
      >
        <el-form-item label="Project Title" prop="title">
          <el-input v-model="projectForm.title" placeholder="Your project title" clearable></el-input>
        </el-form-item>
        <el-form-item label="Project Link" prop="link">
          <el-input v-model="projectForm.link" placeholder="Link to your project" clearable></el-input>
        </el-form-item>
        <el-form-item label="Description" prop="desc">
          <el-input
            v-model="projectForm.desc"
            type="textarea"
            prop="desc"
            placeholder="Project description..."
            maxlength="500"
            :autosize="{minRows: 2}"
            clearable
            show-word-limit
          ></el-input>
        </el-form-item>
        <el-form-item label="Project Picture" prop="img">
          <el-upload
            ref="uploadImg"
            class="upload-demo"
            action="something"
            :on-change="handleChange"
            :on-error="handleError"
            :on-success="handleSuccess"
            :before-remove="beforeRemove"
            :on-remove="handleRemove"
            :on-exceed="handleExceed"
            :file-list="fileList"
            list-type="picture"
            :limit="1"
            :http-request="uploadImg"
          >
            <el-button size="small" type="primary" @click="lockUpdate">Click to upload</el-button>
          </el-upload>
          <el-tag type="primary">jpeg/png files with a size less than 2MB</el-tag>
        </el-form-item>
        <el-form-item>
          <el-button
            type="primary"
            @click="updateProject"
            size="small"
            :disabled="btnDisabled"
          >Update</el-button>
          <el-button type="danger" @click="deleteProject" size="small">Delete</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import { db, storage } from "./../firebase";
export default {
  props: ["project", "fromArray"],
  data() {
    return {
      projectForm: {
        title: "",
        link: "",
        desc: "",
      },
      rules: {
        title: [
          {
            required: true,
            message: "Please input your project title!",
            trigger: "blur",
          },
        ],
        link: [{ type: "url", message: "Invalid URL!", trigger: "blur" }],
        desc: [
          {
            required: true,
            message: "Please enter a short description about your project!",
            trigger: "blur",
          },
        ],
      },
      show: true,
      projectID: -1,
      uploadedFile: null,
      fileList: [],
      fileName: "",
      btnDisabled: false,
      imageURL: "",
    };
  },
  created: function () {
    if (this.$props.fromArray != undefined) {
      this.projectForm.title = this.$props.project.title;
      this.projectForm.link = this.$props.project.link;
      this.projectForm.desc = this.$props.project.desc;
      this.projectID = this.$props.project.id;
      this.fileName = this.$props.project.fileName;
      storage
        .ref()
        .child("projects")
        .child(this.projectID)
        .child(this.fileName)
        .getDownloadURL()
        .then((url) => {
          this.imageURL = url;
        });
    }
  },
  methods: {
    updateProject() {
      this.$refs.projectForm.validate((valid) => {
        if (valid) {
          if (!this.$props.fromArray) {
            this.isFromArray = false;
            this.projectID = makeid(11);
          }
          console.log(this.uploadedFile);
          if (this.uploadedFile != null) {
            this.fileName = this.uploadedFile.name;
            storage.ref().child("projects").child(this.projectID);
            var imageRef = storage
              .ref()
              .child("projects")
              .child(this.projectID);
            imageRef
              .child(this.uploadedFile.name)
              .put(this.uploadedFile)
              .then(() => {
                storage
                  .ref()
                  .child("projects")
                  .child(this.projectID)
                  .child(this.fileName)
                  .getDownloadURL()
                  .then((url) => {
                    this.imageURL = url;
                    db.collection("portfolio")
                      .doc("projects")
                      .collection("projects")
                      .doc(this.projectID)
                      .set({
                        id: this.projectID,
                        title: this.projectForm.title,
                        link: this.projectForm.link,
                        desc: this.projectForm.desc,
                        fileName: this.fileName,
                        imgURL: this.imageURL,
                      });
                  });
              });
          } else {
            db.collection("portfolio")
              .doc("projects")
              .collection("projects")
              .doc(this.projectID)
              .set({
                id: this.projectID,
                title: this.projectForm.title,
                link: this.projectForm.link,
                desc: this.projectForm.desc,
                fileName: this.fileName,
                imgURL: this.imageURL,
              });
          }
          this.$notify.success("Updated successfully!");
        }
      });
    },
    deleteProject() {
      if (!this.$props.fromArray) {
        this.isFromArray = false;
      } else {
        db.collection("portfolio")
          .doc("projects")
          .collection("projects")
          .doc(this.projectID)
          .delete();
        storage
          .ref()
          .child("projects")
          .child(this.projectID)
          .child(this.fileName)
          .delete()
          .then(() => {
            this.$notify.warning("Deleted successfully!");
          })
          .catch((err) => {
            console.error(err);
          });
      }
    },
    checkUpload(file) {
      const isPNG = file.type === "image/png";
      const isJPG = file.type === "image/jpeg";
      const isGIF = file.type === "image/gif";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (isPNG || isJPG || isGIF) {
        if (isLt2M) {
          console.log("Picture is in correct format!");
          return true;
        } else {
          this.$message.error("Picture size cannot exceed 2MB!");
        }
      } else {
        this.$message.error("Picture must be JPEG or PNG or GIF format!");
      }
    },
    handleChange() {
      this.btnDisabled = false;
    },
    handleError(err) {
      this.btnDisabled = false;
      console.error(err);
      this.$message.error("Oops something went wrong! Please try again!");
    },
    handleSuccess(response, file) {
      this.uploadedFile = file;
      this.btnDisabled = false;
    },
    handleRemove(file, fileList) {
      fileList.splice(fileList.indexOf(file));
    },
    handleExceed() {
      this.$message.warning(`You can only upload 1 image!`);
    },
    beforeRemove(file) {
      return this.$confirm(`Cancel the upload of ${file.name} ?`);
    },
    lockUpdate() {
      this.btnDisabled = true;
    },
    uploadImg(params) {
      this.uploadedFile = params.file;
    },
  },
  computed: {
    isFromArray: {
      get: function () {
        if (this.$props.fromArray == undefined) {
          return false;
        }
        return this.$props.fromArray;
      },
      set: function (value) {
        this.show = value;
      },
    },
  },
};

function makeid(length) {
  var result = "";
  var characters =
    "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
  var charactersLength = characters.length;
  for (var i = 0; i < length; i++) {
    result += characters.charAt(Math.floor(Math.random() * charactersLength));
  }
  return result;
}
</script>

