<template>
    <div class='education'>
        <el-card body-style="background-color: #67C23A; color: #fff" v-for="school in sortedArray" :key="school.name" class="box-card" shadow="hover">
            <div slot="header" class="clearfix">
                <div><i>{{school.name}}</i></div>
                <div>{{school.fromDate}} - <span v-if="school.present === true">Present</span><span v-else>{{school.toDate}}</span></div>
            </div>
            <div class="text item"><b>Location: </b><span v-if="school.location_city === ''">{{school.location_country}}</span><span v-else>{{school.location_city}}, {{school.location_country}}</span></div>
            <div class="text item"><b>Degree: </b><span>{{school.degree}}</span></div>
            <div class="text item" v-if="school.cgpa > 0"><b>CGPA: </b>{{school.cgpa}}</div>
            <div class="description-box">
                <div class="text item"><b>Description:&emsp;</b></div>
                <br>
                <div class="text item"><span>{{school.content}}</span></div>
            </div>
        </el-card>        
    </div>
</template>

<script>
import {db} from "./../firebase"
import { compare } from './../scripts/compare';
export default {
    data(){
        return {
            formattedFromDate: "",
            formattedToDate: ""
        }
    },
    firestore(){
        return {
            schools: db.collection("portfolio").doc("education").collection("education")
        }
    },
    computed: {
        sortedArray: function() {
            var newArray = this.schools.slice();
            return newArray.sort(compare);
        }
    }
}
</script>

