export function compare(a,b) {
  if (a.present && b.present) {
    if (a.from < b.from) return 1;
    if (a.from > b.from) return -1;
  } else if (a.present && !b.present) {
    return -1;
  } else if (!a.present && b.present) {
    return 1;
  } else {
    if (a.from < b.from) return 1;
    if (a.from > b.from) return -1;
  }
  return 0;
}