<template>
    <div class='experience'>
        <el-card body-style="background-color: #409EFF; color: #fff" v-for="job in sortedArray" :key="job.title" class="box-card" shadow="hover">
            <div slot="header" class="clearfix">
                <div><span><b>{{job.title}}</b> -- <i>{{job.position}}</i></span></div>
                <div>{{job.fromDate}} - <span v-if="job.present === true">Present</span><span v-else>{{job.toDate}}</span></div>
            </div>
            
            <div class="text item"><b>Location: </b><span v-if="job.location_city === ''">{{job.location_country}}</span><span v-else>{{job.location_city}}, {{job.location_country}}</span></div>
            <div class="description-box">
                <div class="text item"><b>Description:&emsp;</b></div>
                <br>
                <div class="text item"><span>{{job.content}}</span></div>
            </div>
        </el-card>        
    </div>
</template>

<script>
import {db} from "./../firebase"
import { compare } from './../scripts/compare';
export default {
    firestore(){
        return {
            jobs: db.collection("portfolio").doc("experience").collection("experience")
        }
    },
    computed: {
        sortedArray: function() {
            var newArray = this.jobs.slice();
            return newArray.sort(compare);
        }
    }
}
</script>

